import React, { useEffect, useState } from 'react';
import { Map } from '@esri/react-arcgis';

function EsriMapPOC(props) {

    const[source, setSource] = useState("https://dev-env.space/esri.php");

    useEffect(()=>{
        let src = "https://dev-env.space/esri.php?data=" + props.data + "&center=" + props.center;
        console.log('src ------>', src)
        setSource(src);
    }, [props.data])
    
    return (
        <div style={{ width: '100vw', height: '100vh' }}>
            {/* <Map /> */}
            <iframe style={{ width: '100vw', height: '100vh' }} src="https://dev-env.space/esri.php?data="></iframe>
        </div>
    );
}

export default EsriMapPOC;